import React from "react";
import { Link } from "react-router-dom";

// import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
// import { faGlobe } from "@fortawesome/free-solid-svg-icons";
import LogoBrand from "assets/images/logo/logoBrand.jpg";
// import { Dropdown } from "react-bootstrap";

import styleFooter from "styles/footer.module.css";

import facebookIcon from "assets/images/icon/facebookIcon.png";
import instagramIcon from "assets/images/icon/instagramIcon.png";
import linkedinIcon from "assets/images/icon/linkedinIcon.png";
import youtubeIcon from "assets/images/icon/youtubeIcon.png";
// import flagEnglish from "assets/images/icon/flag-english.png";
// import flagIndonesia from "assets/images/icon/flag-indonesia.png";

import { checkToken } from "store/storageToken";

export default function Footer() {
  return (
    <>
      <div className={ `${styleFooter.footerWrap} container-fluid px-3 py-5` }>
        <div className="row d-flex justify-content-around">
          <div className="col-6 col-md-2">
            <img
              src={LogoBrand}
              className="align-top logo-brand"
              alt="Cakrawala Bahasa Logo"
            />
            {/* <div className="row mt-2" inline="true">
              <Dropdown>
                <FontAwesomeIcon icon={faGlobe} /> Eng
                <Dropdown.Toggle
                  split
                  variant="light"
                  id="dropdown-split-basic"
                  size="sm"
                  style={{ backgroundColor: "#fff" }}
                />
                <Dropdown.Menu>
                  <Dropdown.Item href="/">
                    <img
                      src={flagEnglish}
                      className={`d-inline-block align-top ${styleFooter.icon}`}
                      alt="Youtube Icon"
                    />
                    English
                  </Dropdown.Item>
                  <Dropdown.Item href="/">
                    <img
                      src={flagIndonesia}
                      className={`d-inline-block align-top ${styleFooter.icon}`}
                      alt="Youtube Icon"
                    />
                    Indonesia
                  </Dropdown.Item>
                </Dropdown.Menu>
              </Dropdown>
            </div> */}
          </div>
          <div className="col-6 col-md-2">
            <p className="text-bold text-secondary">Menu</p>
            <Link className="text-decoration-none" to="/our-programs">
              <p className="text-secondary">Our Programs</p>
            </Link>
            <Link className="text-decoration-none" to="/news">
              <p className="text-secondary">News</p>
            </Link>
          </div>
          <div className="col-6 col-md-2 mt-3 mt-md-0">
            <Link className="text-decoration-none" to="/about-us">
              <p className="text-bold text-secondary">About</p>
            </Link>
            {
              checkToken() !== undefined && checkToken() !== null &&
              <Link className="text-decoration-none" to="/profile">
                <p className="text-secondary">Profile</p>
              </Link>
            }
          </div>
          <div className="col-6 col-md-2 mt-3 mt-md-0">
            <Link className="text-decoration-none" to="/contact">
              <p className="text-bold text-secondary">Contact</p>
            </Link>
          </div>
          <div className="col-8 col-md-2 offset-md-2">
            <p className="text-bold text-secondary">Follow us:</p>
            <div>
              <a
                href="https://mobile.facebook.com/cakrawalabahasaofficial/"
                className="text-decoration-none p-1"
                target="blank"
              >
                <img
                  src={facebookIcon}
                  className={`d-inline-block align-top ${styleFooter.icon}`}
                  alt="facebook Icon"
                />
              </a>
              <a
                href="https://www.linkedin.com/company/cakrawala-bahasa/"
                className="text-decoration-none p-1"
                target="blank"
              >
                <img
                  src={linkedinIcon}
                  className={`d-inline-block align-top ${styleFooter.icon}`}
                  alt="LinkedIn Icon"
                />
              </a>
              <a
                href="https://www.instagram.com/cakrawalabahasa/"
                className="text-decoration-none p-1"
                target="blank"
              >
                <img
                  src={instagramIcon}
                  className={`d-inline-block align-top ${styleFooter.icon}`}
                  alt="Instagram Icon"
                />
              </a>
              <a
                href="https://www.youtube.com/channel/UCI3WvtmgNv2ifWBdiAGdWYA"
                className="text-decoration-none p-1"
                target="blank"
              >
                <img
                  src={youtubeIcon}
                  className={`d-inline-block align-top ${styleFooter.icon}`}
                  alt="Youtube Icon"
                />
              </a>
            </div>
          </div>
        </div>
        <hr className={styleFooter.hrFooter} />
        <div className="row">
          <div className="col-6 col-md-2">
            <small>Cakrawala Bahasa @2022</small>
          </div>
          <div className="col-6 col-md-2">
            <small>Terms and Privacy</small>
          </div>
        </div>
      </div>
    </>
  );
}
